import React, { Component } from 'react'
import { Container, Form, FormGroup, Input, Button, Label, Row } from 'reactstrap'
import axios from 'axios'

import { Link } from 'react-router-dom'
import { API_ROOT } from '../statics/index'

export default class SignUp extends Component {
    state = {};

    _getAccount = (e) => {
        let name = e.target.name;
        let value = e.target.value
        this.setState({
            [name]: value
        })
    }

    _onSignup = (e) => {
        e.preventDefault();
        axios.defaults.withCredentials = true;
        axios
            .post(`/api/users`, {
                username: this.state.username,
                password: this.state.password,
                email: this.state.email,
                avatarUrl: this.state.avatarUrl
            }, {
                    validateStatus: (status) => {
                        return status >= 200 && status < 500
                    }
                }
            )
            .then(response => {
                // console.log(response.data.success === 1)
                this.setState({ data: response.data })
                if (response.data.success === 1) {
                    // axiosCheck.then(res => console.log(res.data))
                    //     .catch(err => console.log(err))
                    //this.props.history.push("/")
                    window.location.href = "/"
                }
                else {
                    alert("SAI TAI KHOAN")
                }
            }
            )
            .catch(err => console.error(err))
    }

    render() {


        return (
            <Container>
                <br /><br /><br /><br /><br /><br />
                <Row><Container className="border">
                    <Form onSubmit={this._onSignup}>
                        <FormGroup>
                            <h3>To ti khon</h3>
                        </FormGroup>
                        <FormGroup>
                            <Label>Tn ng nhp</Label>
                            <Input onChange={this._getAccount} name='username' type='text' placeholder='Tn ng nhp...' />
                        </FormGroup>
                        <FormGroup>
                            <Label>Mt khu</Label>
                            <Input onChange={this._getAccount} name='password' type='password' placeholder='Mt khu...' />
                        </FormGroup>
                        <FormGroup>
                            <Label>Email</Label>
                            <Input onChange={this._getAccount} name='email' type='text' placeholder='example@abc.com' />
                        </FormGroup>
                        <FormGroup>
                            <Label>Link nh i din</Label>
                            <Input onChange={this._getAccount} name='avatarUrl' type='text' placeholder='link nh...' />
                        </FormGroup>
                        <FormGroup>
                           <Button type='submit' color='primary'>ng K</Button>{'  '}
                            <Link to='/'><Button type='submit' color='danger'>Hy</Button></Link>
                        </FormGroup>
                    </Form>
                </Container></Row>
            </Container>
        )
    }
}


