import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, Form, FormGroup, Input, Container } from 'reactstrap';

import axiosCheck from '../networks/axiosCheck'
import axios from 'axios'

import { API_ROOT } from '../statics/index'

// import { Link } from 'react-router-dom'


export default class CreateTable extends Component {

    constructor(props) {
        super(props);
        this.state = {
            modal: false
        };

        this.toggle = this.toggle.bind(this);
    }

    toggle() {
        this.setState({
            modal: !this.state.modal
        });
    }

    _authCheck = () => {
        axiosCheck
            .then(res => console.log(res.data))
            .catch(err => console.log(err))
    }

    _getTable = (e) => {
        let name = e.target.name;
        let value = e.target.value
        this.setState({
            [name]: value
        })

    }

    _addFood = (e) => {
        e.preventDefault();
        axios
            .post(`${API_ROOT}/tables`, {
                name: this.state.name,
                imageUrl: this.state.imageUrl,
                price: this.state.price,
            }, {
                    validateStatus: (status) => {
                        return status >= 200 && status < 500
                    }
                }
            )
            .then(response => {
                console.log(response)
                if (response.data.success === 1) {

                    alert("Food added")
                    window.location.href= '/'
                }
                else {
                    alert("loi ")
                }
            }
            )
            .catch(err => console.error(err))
    }
    render() {

        return (
            <div>
                <Container>
                    <Button color="danger" onClick={this.toggle}>{this.props.buttonLabel}Them food</Button>
                    <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
                        <ModalHeader toggle={this.toggle}><h3>New table</h3></ModalHeader>
                        <ModalBody>
                            <Form onSubmit={this._addFood} >
                                <FormGroup>
                                    <h5>name</h5><Input onChange={this._getTable} name='name' placeholder='Name...' />
                                </FormGroup>
                               
                                <FormGroup>
                                    <h5>price</h5><Input onChange={this._getTable} name='price' type='text' placeholder='Price...' />
                                </FormGroup>
                                <FormGroup>
                                    <h5>imageUrl</h5><Input onChange={this._getTable} name='imageUrl' type='text' placeholder='url' />
                                </FormGroup>
                                <FormGroup className="text-right">
                                    <Button type='submit'>Them mon</Button>{' '}
                                    <Button color="secondary" onClick={this.toggle}>Hy</Button>
                                </FormGroup>
                            </Form>
                        </ModalBody>
                    </Modal>
                </Container>
            </div>
        );
    }
}


